@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@700&family=Lora:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@400;700&family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Readex+Pro:wght@600&display=swap);

.Title {
    margin-top: 0;
    margin-bottom: 100px;
    padding-top: 150px;
    text-align: center;
    font-size: 3.5em;
    font-family: 'Cormorant Garamond', serif;
    letter-spacing: 30px;
    color: rgb(59, 3, 3);
    text-shadow: 1px 1px rgb(59, 3, 3);
}

.InputBox {
    display: flex;
    justify-content: center;
    grid-gap: 10px;
    gap: 10px
}

.Input {
    padding: 10px 10px;
    width: 60%;
    border-radius: 10px;
    border: 0.5px solid black;
    font-size: 1.2em;
    box-shadow: 0.3em 0.3em 0.3em rgba(206, 205, 205, 0.441);
}

.Enter {
    padding: 10px 20px;
    background-color: #383737;
    color: white;
    font-size: 1.5em;
    text-align: center;
    border: 1px solid black;
    border-radius: 10px;
    box-shadow: 0.2em 0.2em 0.2em rgba(206, 205, 205, 0.441);
    transition: 0.15s;
}

.Enter:hover {
    cursor: pointer;
    transform: translateX(-0.1em) translateY(-0.1em);
}

@media(max-width: 540px) {
    .Title {
        font-size: 2em;
        margin-bottom: 50px;
    }

    .Input {
        padding: 5px 5px;
    }
}
.signInBackground {
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 400px;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    margin-top: 90px;
    text-align: center;
    border-radius: 10px;
    box-shadow:  0.3em 0.3em 0.3em #32191eaf,
                -0.2em 0.3em 0.3em #32191eaf;
    background-color: #090405bf;
    padding: 20px 40px;
}

.logintitle {
  margin: 20px 0 30px 0;
  font-family: 'Merriweather Sans', sans-serif;
  font-size: 1.8em;
  font-weight: 700;
  color: white;
  letter-spacing: 0.3em;
  text-shadow: none;
}

.control {
  position: relative;
  margin: 10px 0;
  width: 90%;
}

.control input {
  background-color: rgba(0, 0, 0, 0);;
  border: 0;
  border-bottom: 2px #fff solid;
  display: block;
  width: 100%;
  padding: 15px 0;
  font-size: 15px;
  color: white;
  letter-spacing: 0.2em;
}

.control input:focus,
.control input:valid {
  outline: 0;
  border-bottom-color: white;  
}

::-webkit-input-placeholder {
    color: white;
}

.logininbutton {
  cursor: pointer;
  width: 40%;
  background: #ddd2d2;
  padding: 15px;
  margin-top: 40px;
  font-family: 'Merriweather Sans', sans-serif;
  letter-spacing: 0.3em;
  font-size: 16px;
  border: 0;
  border-radius: 5px;
  color: black;
  transition: 0.3s;
}

.logininbutton:focus {
  outline: 0;
}

.logininbutton:hover {
    transform: scale(1.1);
}

.logininbutton:active {
  transform: scale(0.98);
}

.textForRegister {
    margin-top: 45px;
}

.textForRegister, .register {
    font-size: 1em;
    color: white;
    letter-spacing: 0.1em;
}

.register {
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    outline: none;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    transition: ease-in-out;
    color: white;
}

.register:hover {
    transform: scale(1.1);
}

@media(max-width: 540px) {
  .signInBackground {
    width: 250px;
  }

  .logintitle {
    font-size: 1em;
    line-height: 2em;
  }

  .control input {
    font-size: 10px;
  }

  .logininbutton {
    font-size: 10px;
    padding: 10px;
    letter-spacing: 0;
  }

  .textForRegister, .register {
    font-size: 0.8em;
  }
}

.Navibar {
    transition-timing-function: ease-in;
    transition:0.5s;
}

.Buttons {
    text-align: right;
    width: 100%;
}

.Buttons button {
    background: none;
    border: none;
    font-size: 1.2em;
    margin: 10px 20px 5px 20px;
    color: rgb(56, 55, 55);
}

.Buttons button:hover {
    color: rgb(122, 50, 50);
    text-decoration: underline;
}

.UserIcon {
    font-size: 2em;
    margin-left: 10px;
    margin-right: 5px;
}

.UserNameButton:hover {
    color: rgb(56, 55, 55);
    text-decoration: none;
}

.MobileButton {
    display: none;
}

@media(max-width: 700px) {
    .MobileButton {
        display: flex;
        justify-content: flex-end;
        padding: 10px 30px 10px 0;
        font-size: 2em;
        position: absolute;
        width: 100%;
        z-index: 1;
        cursor: pointer;
    }

    .Buttons {
        padding-top: 100px;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 100vh;
        background-color: white;
        position: absolute;
        left: 200%;
        transition: all 0.7s ease;
    }

    .Buttons.active {
        left: 0;
        transition: all 0.7s ease;
    }
}

body {
    margin: 0;
}

.Background {
    position: absolute;
    margin: 0;
    background-image: url(/static/media/Background4.41d84077.jpg);
    opacity: 0.8;
    width: 100vw;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.735);
    z-index: -1;
}


.ResultBackground {
    background-color: #515369;
    background-color: #e7e8ee;
    background: hsla(235, 10%, 47%, 1);
    background: linear-gradient(0deg, hsla(235, 10%, 47%, 1) 0%, hsla(220, 45%, 91%, 1) 100%);
    background: -webkit-linear-gradient(0deg, hsla(235, 10%, 47%, 1) 0%, hsla(220, 45%, 91%, 1) 100%);   
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#6B6D84", endColorstr="#DDE4F2", GradientType=1 );
    width: 100vw;
    min-height: 100vh;
}
.JobPage .card {
    color: white;
    background-color: #23191baf;
    box-shadow:  0.3em 0.3em 0.3em #32191eaf,
                -0.2em 0.3em 0.3em #32191eaf;
    transition: 0.15s;
}

.JobPage .card-header {
    background-color: #fffcf2;
    color: black;
}

.JobPage .card-body {
   display: grid;
   grid-template-columns: 1fr 3fr;
}

.JobPage .icon {
    margin-top: 20px;
}

.JobPage .NameAndTitle {
    display: flex;
    justify-content: center;
    align-items: baseline;
    grid-gap: 40px;
    gap: 40px;
    margin: 5px 0 30px 0;
}

.JobPage .JobName {
    font-size: 2em;
}

.JobPage .CompanyAndLocation {
   display: flex;
   justify-content: center;
   grid-gap: 20px;
   gap: 20px;
   margin-top: 40px;
}

.JobPage .card:hover {
    transform: translateX(-0.5em) translateY(-0.4em);
}

.JobPage .DetailButton {
    margin-top: 15px;
    color: whitesmoke;
    text-decoration: underline;
}

@media(max-width: 760px) {
    .JobPage .card-body {
        display: grid;
        grid-template-columns: 1fr;
    }

    .JobPage .icon {
        margin-top: 0;
        margin-bottom: 10px;
    }
    .JobPage .JobName {
        font-size: 1.5em;
    }

    .CompanyAndLocation {
        flex-direction: column;
        grid-gap: 20px;
        gap: 20px;
    }
}

@media(max-width: 540px) {
    .JobPage .JobName {
        font-size: 1em;
    }

    .CompanyAndLocation {
        flex-direction: column;
        grid-gap: 20px;
        gap: 20px;
    }

    .CompanyAndLocation .card-title {
        font-size: 0.8em;
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 10px;
        gap: 10px;
    }

    .JobPage .DetailButton {
        font-size: 0.8em;
    }
}
.ResultTitle {
    text-align: center;
    padding-top: 100px;
    font-family: 'Readex Pro', sans-serif;
    color: #282a37;
}

.JobList {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
    width: 50%;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 100px;
}



.card {
    margin-bottom:80px;
}

.JobDetail {
    display: flex;
    justify-content: center;
    padding-top: 80px;
    min-height: 50vh;
}

.JobDetail .card {
    width: 80%;
    background-color: rgba(251, 251, 251, 0.886);
    box-shadow:  0.1em 0.2em 0.2em #f7f7f7b8,
                -0.1em 0.2em 0.2em #f7f7f7b8;
}

.JobDetail .card-header {
    font-size: 1.5em;
    padding-top: 15px;
    padding-bottom: 15px;
    background-color: #3a161dd1;
    color: white;
    box-shadow:  0.1em 0em 0.1em #3a161dd1,
                -0.1em 0em 0.1em #3a161dd1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.JobDetail .updateButton {
    margin-right: 20px;
}

.JobDetail .CompanyAndLocation, .JobDetail .Employer {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.JobDetail .postdate {
    margin-bottom: 25px;
}

.JobDetail .Employer {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    grid-gap: 0;
    gap: 0;
}

.Employer div {
    margin-bottom: 10px;
}

.DescriptionTitle {
    margin-top: 50px;
}

.DescriptionDisplay {
    margin-top: 10px;
    margin-bottom: 20px;
    padding-top: 10px;
    position: relative;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    width: 90%;
}

.likeButton {
    width: 100px;
}

.form-select {
    max-width: 200px;
}

@media(max-width: 540px) {
    .firstPart {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .firstPart .card-title, .DescriptionTitle {
        font-size: 1em;
        text-align: center;
    }

    .JobDetail .Employer {
        font-size: 0.8em;
    }

    .likeButton {
        width: 80px;
    }
}
.Container {
    padding-top: 100px;
    padding-bottom: 70px;
}

.CreateJobPage {
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 70%;
    max-width: 1100px;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    margin-bottom: 30px;
    border-radius: 10px;
    box-shadow:  0.3em 0.3em 0.3em #32191eaf,
                -0.2em 0.3em 0.3em #32191eaf;
    background-color: #23191baf;
    padding: 20px 40px 40px 40px;
}

.CreateJobTitle {
    margin: 20px 0 30px 0;
    font-family: 'Merriweather Sans', sans-serif;
    font-size: 1.8em;
    font-weight: 700;
    color: white;
    letter-spacing: 0.3em;
    text-shadow: none;
  }

.CreateJobPage .control {
    position: relative;
    margin: 10px 0;
    width: 90%;
    display: flex;
    justify-content: space-between;
    grid-gap: 40px;
    gap: 40px;
  }

  
.CreateJobPage .control input {
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    border-bottom: 2px white solid;
    display: block;
    width: 40%;
    padding: 15px 0;
    font-size: 15px;
    letter-spacing: 0.2em;
}

.fileInput {
    position: relative;
    margin: 40px 0 10px 0;
    width: 90%;
    display: flex;
    grid-gap: 40px;
    gap: 40px;
    color: white;
}

.control-2 {
    display: flex;
    flex-direction: column;
    padding-top: 20px;
}

.control-2 .description {
    letter-spacing: 0.2em;
    color: white;
}

.Editor {
    color: black;
    background-color: white;
    min-height: 300px;
    margin-bottom: 20px;
    letter-spacing: 0;
}

.Editor input {
    color: black;
}

.CreateJobPage input:focus,
.CreateJobPage input:valid, 
.CreateJobPage textarea {
    outline: 0;
    border-bottom-color: white;  
}
  
::-webkit-input-placeholder {
    color: white;
}

.CreateJobPage form {
    display: flex;
    flex-direction: column;
}

.CreateJobPage .logininbutton {
    background-color: white;
}


@media(max-width: 800px) {
    .CreateJobTitle {
        font-size: 1.5em;
    }

    .fileInput {
        font-size: 12px;
        flex-direction: column;
        grid-gap: 5px;
        gap: 5px;
    }

    .description {
        font-size: 12px;
        margin-top: 10px;
        margin-bottom: 5px;
    }

    ::-webkit-input-placeholder {
        color: white;
        font-size: 12px;
        letter-spacing: 2px;
    }

    .Editor {
        font-size: 12px;;
    }
}

@media(max-width: 540px) {
    .CreateJobPage .control {
        position: relative;
        width: 100%;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        grid-gap: 20px;
        gap: 20px;
    }

    .CreateJobPage .control input {
        width: 100%;
    }

    .fileInput {
        position: relative;
        margin: 20px 0 10px 0;
        width: 100%;
        grid-gap: 0;
        gap: 0;
    }
}
.buttonGroups {
    display: flex;
    grid-gap: 40px;
    gap: 40px;
}

.buttonGroups .btn-light {
    padding-left: 20px;
    padding-right: 20px;
}

.buttonGroups .btn-light:hover {
    background-color: #adb5bd;
    border: 1px solid #adb5bd;
}

@media(max-width: 540px) {
    .buttonGroups .btn-light {
       padding-left: 5px;
       padding-right: 5px;
    }
}
